@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@0;1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=PT+Sans:ital@0;1&display=swap');
//colors
$color-band: #75efff;
$color-bg: #101c31;
$color-font: #fff;
$color-border: #ccc;


//media width
$large: 1100px;
$ex-medium: 900px;
$medium: 768px;
$small: 550px;
$extraSmall: 450px;

//padding marign
$pm-15: 150px;
$pm-10: 100px;
$pm-8: 80px;
$pm-6: 60px;
$pm-5: 50px;
$pm-4: 40px;
$pm-2: 20px;
$pm-1: 10px;

//font
$monospace: 'Roboto Mono', monospace;;
$heading-font: 'Roboto', sans-serif;
$all-font: 'Poppins', sans-serif;
$liber: 'Libre Baskerville', serif;

$transition: all 0.3s ease-out;



.sideScroller{
    position: fixed;
    right: 50px;
    top: 50%;
    transform: translateY(-50%);
    width: 50px;
    @media (max-width: $medium) {
        display: none;
    }
    ul{
        list-style: none;
        li{
            margin: 20px 0;
            text-align: right;
            display: flex;
            justify-content: flex-end;
            a{
                display: block;
                width: 60%;
                height: 3px;
                background-color: $color-border;
                cursor: pointer;
                transition: $transition;
                &.active{
                    width: 100%;
                    background-color: $color-band;
                }
            }
        }
    }
}